.top {
  width: 100%;
  height: 90px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
}

.top div {
  margin-right: 6px;
}

.top .left-box {
  width: 132px;
  background-color: #061547;
  border-radius: 10px;
}

.top .right-box {
  width: calc(100% - 154px);
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #061547;
  border-radius: 10px;
}

.header-box {
  padding: 5px 10px;
  text-align: center;
}

.header-box .title {
  font-size: 14px;
  color: #fff;
  margin-top: 5px;
  text-align: left;
}

.header-box .number {
  font-weight: 700;
  font-size: 30px;
  color: #61dfff;
  height: 50px;
  line-height: 50px;
}

.top .right-box .header-box {
  flex: 1;
  border-right: rgba(255, 255, 255, .1) dotted 1px;
}

.content {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}

.highcharts-map {
  width: 49%;
  height: 900px;
  border: 2px solid #134f84;
  position: relative;
}

#allmap {
  width: 90%;
  height: 850px;
}

.list-map {
  width: 49%;
}

.list-box {
  height: 480px;
  margin-bottom: 20px;
  overflow: auto;
  border: 2px solid #134f84;
  background: #061547;
}

.list-box .active {
  background: #0c1d58;
}

.list-box-con {
  display: flex;
  line-height: 60px;
  cursor: pointer;
  margin: 0 40px;
  border-bottom: rgba(255, 255, 255, .1) dotted 1px;
  text-align: left;
  font-size: 16px;
  color: #3bb8fe;
}

.list-box-con p {
  flex: 1;
  margin: 0;
}

.list-box-con p.samc_name {
  flex: 4;
}

.list-map .map {
  width: 100%;
  height: 400px;
  border: 2px solid #134f84;
}

#map {
  width: 100%;
  height: 100%;
}